import {connect} from "react-redux";
import React, {Component} from "react";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getNeedSearchCheckoutErrors, isBusinessClient} from "../../selectors";
import {dismissBackendValidationErrors, gotoCartSummary} from "../../actions/app";


class SearchNeedModalView extends Component {

    onClose(){
        this.props.dismiss();
    }

    getText() {
        if(this.props.isB2B !== undefined && !!this.props.descriptions.textInfoB2C && !! this.props.descriptions.textInfoB2B) {
            return this.props.isB2B ? this.props.descriptions.textInfoB2B : this.props.descriptions.textInfoB2C;
        } else {
            return this.props.textInfo;
        }
    }

    renderInfo() {
        return (<span>{this.getText()}</span>);
    }

    render() {
        return (
            <OraModal showClose={true}  open={this.props.errors && this.props.errors.length > 0}  onClose={this.onClose.bind(this)} size="medium" id={'search-need-customer-modal'}>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row u-spacing-top-m">
                        <div className="l-col l-col-12">
                            {this.renderInfo()}
                        </div>
                    </div>
                </div>
            </OraModal>);
    }
}


let mapStateToProps = state => ({
    errors: getNeedSearchCheckoutErrors(state),
    isB2B : isBusinessClient(state)
});
let mapDispatchToProps = dispatch => ({
    dismiss: () => dispatch(dismissBackendValidationErrors()),
    action: () => dispatch(gotoCartSummary())
});

const SearchNeedModal = connect(mapStateToProps, mapDispatchToProps)(SearchNeedModalView);
export default SearchNeedModal;
